import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { SanitizeHtml } from '../utility/SanitizeHtml';
import '../resource/components/AboutWork.css';
import '../resource/components/Stargazing.sass';
import { content } from '../data/Work.json';
import StargazingBackground from './StargazingBackground';

const style = {
  '--left-margin': '300px',
  '--z-index': '-1'
};
const moonStyle = {
  '--left-margin': '700px',
  '--z-index': '-1'
};

function createComponent() {
  return (
    <Container className="work-container">
      <StargazingBackground style={style} moonStyle={moonStyle} />
      <hr className="breaker" />
      <Row className="justify-content-center center">
        <b>About Me</b>
      </Row>
      <hr className="breaker" />
      <Row>
        <Col>
          <SanitizeHtml className="content" html={content[0]} />
        </Col>
        <Col>
          <SanitizeHtml className="content" html={content[1]} />
        </Col>
      </Row>
      <hr className="breaker" />
      <Row className="justify-content-center center">
        <b>Interests</b>
      </Row>
      <hr className="breaker" />
      <Row>
        <Col>
          <SanitizeHtml className="content" html={content[2]} />
        </Col>
        <Col>
          <SanitizeHtml className="content" html={content[3]} />
        </Col>
      </Row>
    </Container>
  );
}

class AboutWork extends Component {
  render() {
    return createComponent();
  }
}

export default AboutWork;
