import React from 'react';
import '../resource/components/Stargazing.sass';

function StargazingBackground(props) {
  const { style, moonStyle } = props;
  return (
    <React.Fragment>
      <div className="stars" style={style} />
      <div className="stars2" style={style} />
      <div className="stars3" style={style} />
      <div className="moon" style={moonStyle} />
    </React.Fragment>
  );
}

export default StargazingBackground;
